@import url('https://fonts.googleapis.com/css2?family=Inknut+Antiqua&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

// flexer
%flexer {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
// containers
.container {
    width: clamp(300px, 95vw, 1400px);
    margin-inline: auto;
}

// heading and text
.h1 {
    font-size: clamp(1.5rem, 2vw, 3.5rem);
    font-weight: 900;
    font-family: 'Inknut Antiqua', serif;
}

.h2 {
    font-size: clamp(1rem, 2.5vw, 3.5rem);
    font-family: 'Roboto', sans-serif;
}

.p {
    font-size: clamp(0.8rem, 1.3vw, 1.5rem);
    font-family: 'Roboto', sans-serif;
}
